import Home from "../../pages/Home";
import Profile from "../../pages/Profile";
import ontacts from "../../pages/ontacts";
import { HOME_ROUTE, ONTACTS_ROUTE, ADMIN_ROUTE } from "../../utils/consts";

export const adminRoutes = [
  {
    path: ADMIN_ROUTE,
    Element: <Profile />,
  },
];

export const publicRoutes = [
  {
    path: HOME_ROUTE,
    Element: <Home />,
  },
  {
    path: ONTACTS_ROUTE,
    Element: <ontacts />,
  },
];
